<template>
  <div class="operate-task-log">
    <div class="topTitle">
      <span class="topTitle-span1">过程管理</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">过程内容</span>
      <span class="topTitle-span1">
        <img src="../../../assets/topTitle1.png" alt />
      </span>
      <span class="topTitle-span1">案件协查</span>
      <div class="button-content">
        <Button
          type="primary"
          @click="addfun('addFlag')"
          v-authcontrol="{menuCode:'108001002007001'}"
          v-if="firstLoginFlag"
        >
          <i class="ivu-icon ivu-icon-md-add"></i>新增
        </Button>
        <Button
          type="primary"
          v-authcontrol="{menuCode:'108001002007004'}"
          v-if="firstLoginFlag"
          @click="exportFn"
        >
          <i class="ivu-icon ivu-icon-md-arrow-up"></i>导出
        </Button>
      </div>
    </div>
    <div class="contentCellBox">
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>查询条件
        </div>
        <div class="contentCellBody1">
          <Form ref="formValidate" :model="formValidate" :label-width="120" inline>
            <Row>
              <FormItem label="执行机构：">
                <Input v-model="formValidate.val2" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <!-- <FormItem label="执行省份：">
                <Input v-model="formValidate.val3" placeholder="请输入" style="width:200px"></Input>
              </FormItem>-->
              <FormItem label="执行省份：">
                <Select v-model="formValidate.val3" placeholder="请选择地区范围" style="width:200px">
                  <Option value>未选择</Option>
                  <Option
                    v-for="item in regionLsit"
                    :value="item.code"
                    :key="item.code"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="管理部门：">
                <treeselect
                  v-model="formValidate.val9"
                  valueConsistsOf="ALL" 
              :limit="3"
                  :multiple="true"
                  :options="data1"
                  placeholder="请选择"
                  :max-height="100"
                  style="width:200px"
                />
              </FormItem>
            </Row>
            <Row>
              <FormItem label="执行人：">
                <Input v-model="formValidate.val4" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="协查日期：">
                <DatePicker
                  :value="formValidate.val5"
                  @on-change="formValidate.val5=$event"
                  format="yyyy/MM/dd"
                  type="daterange"
                  placement="bottom-end"
                  placeholder="请选择时间"
                  style="width: 200px"
                ></DatePicker>
              </FormItem>
              <FormItem label="被调查人客户名称：">
                <Input v-model="formValidate.val6" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
            </Row>
            <Row>
              <FormItem label="业务名称：">
                <Input v-model="formValidate.val7" placeholder="请输入" style="width:200px"></Input>
              </FormItem>
              <FormItem label="状态：">
                <Select v-model="formValidate.val8" filterable style="width:200px">
                  <Option value>请选择</Option>
                  <Option
                    :value="item.code"
                    v-for="(item,index) in PROCESS_CHECK_STATE"
                    :key="index"
                  >{{item.name}}</Option>
                </Select>
              </FormItem>
            </Row>
            <Row>
              <div class="btnClass">
                <Button type="primary" @click="handleSubmit('formValidate',0)">查询</Button>
                <Button type="primary" @click="handleSubmit('formValidate',1)">精确查询</Button>
              </div>
            </Row>
          </Form>
        </div>
      </div>
      <div class="contentCellBox">
        <div class="contentCellTitle">
          <div class="contentCellTitle-1"></div>数据列表
        </div>
        <div class="contentCellBody">
          <Row>
            <table class="tableBox">
              <thead>
                <tr>
                  <td v-for="(item,index) in tableHeader">{{item.name}}</td>
                </tr>
              </thead>
              <tbody>
                <tr v-for="(item,index) in tableBody">
                  <td>{{pageSize*(pageNum-1)+index+1}}</td>
                  <td style="width:100px">
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002007002'}"
                      v-if="firstLoginFlag"
                      @click="addfun('modifyFlag',item.busId,item.id,item.extraCode)"
                    >修改</span>
                    <span
                      class="spanBox"
                      @click="delItem(item.id,item.extraCode)"
                      v-authcontrol="{menuCode:'108001002007003'}"
                      v-if="firstLoginFlag"
                    >删除</span>
                    <span
                      class="spanBox"
                      v-authcontrol="{menuCode:'108001002007007'}"
                      v-if="firstLoginFlag"
                      @click="addfun('appendFlag',item.busId,item.id,item.flag)"
                    >追加</span>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.assistWork"
                    >
                      <span
                        slot="reference"
                        class="spanBox textMaxWidth disBlock textOverflow"
                        @click="watchTableCellFun(item,item.busId,item.id)"
                      >{{item.assistWork}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.agent"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.agent}}</span>
                    </el-popover>
                  </td>

                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.executeOrg"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.executeOrg}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.executeProvince"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.executeProvince}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.executor"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.executor}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.executorId"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.executorId}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.agentTime"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.agentTime}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <p
                      v-for="(item2,index2) in item.clientIds"
                      :key="index2"
                      class="pClass"
                    >{{item2}}</p>
                  </td>
                  <td>
                    <p
                      v-for="(item2,index2) in item.clientOldIds"
                      :key="index2"
                      class="pClass"
                    >{{item2}}</p>
                  </td>
                  <td>
                    <p
                      v-for="(item2,index2) in item.clientNames"
                      :key="index2"
                      class="pClass"
                    >{{item2}}</p>
                  </td>
                  <td>
                    <p
                      v-for="(item2,index2) in item.certificateTypes"
                      :key="index2"
                      class="pClass"
                    >{{item2}}</p>
                  </td>
                  <td>
                    <p
                      v-for="(item2,index2) in item.certificateIds"
                      :key="index2"
                      class="pClass"
                    >{{item2}}</p>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.freezingNum"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.freezingNum}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.caseType"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.caseType}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.involvedMoney"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.involvedMoney}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.exchangeNum"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.exchangeNum}}</span>
                    </el-popover>
                  </td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.businessName"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.businessName}}</span>
                    </el-popover>
                  </td>
                  <td
                    class="spanBox textOverflow textMaxWidth"
                    @click="showProcess(item)"
                  >{{item.flag}}</td>
                  <td>{{item.ifShow=='true'?'是':'否'}}</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.content"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.content}}</span>
                    </el-popover>
                  </td>
                  <td
                    class="spanBox textOverflow textMaxWidth"
                    @click="watchFileFun(item.busId,item.id,item)"
                  >查看</td>
                  <td>
                    <el-popover
                      placement="top-start"
                      width="200"
                      trigger="hover"
                      :content="item.manager"
                    >
                      <span
                        slot="reference"
                        class="textMaxWidth disBlock textOverflow"
                      >{{item.manager}}</span>
                    </el-popover>
                  </td>
                  <td
                    class="spanBox textOverflow textMaxWidth"
                    @click="watchAppendFun(item,item.busId,item.id)"
                  >查看</td>
                  <td class="textOverflow textMaxWidth">{{item.createTime}}</td>
                  <td class="textOverflow textMaxWidth">{{item.creator}}</td>
                  <td class="textOverflow textMaxWidth">{{item.updateTime}}</td>
                  <td class="textOverflow textMaxWidth">{{item.updater}}</td>
                </tr>
              </tbody>
            </table>
          </Row>
        </div>
        <div class="flexCenter pageBox">
          <Page
            :total="total"
            :current.sync="pageNum"
            show-elevator
            show-sizer
            @on-change="pageChangeFun"
            @on-page-size-change="pageSizeChange"
          ></Page>
        </div>
      </div>
    </div>
    <!-- ===========弹框 -->
    <!-- <Button @click="modal1 = true">Custom header and footer</Button> -->
    <Modal :mask-closable="false" v-model="modal1" width="1000" class="dialog" title="设置">
      <div style="height:400px;overflow-y: auto;" class="modelSonDiv">
        <Form
          ref="formValidateModel1"
          :model="formValidateModel1"
          :rules="ruleValidate"
          :label-width="120"
        >
          <FormItem label="流程标题" prop="bpmTitle">
            <Input v-model="formValidateModel1.bpmTitle" placeholder="请输入" :disabled="addFlag"></Input>
          </FormItem>
          <FormItem label="协助事项">
            <CheckboxGroup v-model="formValidateModel1.val1">
              <Checkbox
                v-for="item in assistWorks"
                :label="item.code"
                :key="item.code"
                :disabled="addFlag"
              >{{item.name}}</Checkbox>
            </CheckboxGroup>
          </FormItem>
          <FormItem label="经办人" prop="val2">
            <Input v-model="formValidateModel1.val2" placeholder="请输入" :disabled="addFlag"></Input>
          </FormItem>
          <FormItem label="执行机构" prop="val3">
            <Input v-model="formValidateModel1.val3" placeholder="请输入" :disabled="addFlag"></Input>
          </FormItem>
          <FormItem label="执行省份">
            <Select v-model="formValidateModel1.val4" :disabled="addFlag">
              <Option
                :value="item.code"
                v-for="(item,index) in executeProvinces"
                :key="index"
              >{{item.name}}</Option>
            </Select>
          </FormItem>
          <FormItem label="执行人" prop="val5">
            <Input v-model="formValidateModel1.val5" placeholder="请输入" :disabled="addFlag"></Input>
          </FormItem>
          <FormItem label="执行人证件号码" prop="val6">
            <Input v-model="formValidateModel1.val6" placeholder="请输入" :disabled="addFlag"></Input>
          </FormItem>
          <FormItem label="协查日期">
            <DatePicker
              @on-change="formValidateModel1.val7=$event"
              :value="formValidateModel1.val7"
              :disabled="addFlag"
              format="yyyy-MM-dd"
              type="date"
              placement="bottom-end"
              placeholder="请选择时间"
              style="width:100%"
            ></DatePicker>
          </FormItem>
          <FormItem label="被调查人">
            <!-- ============== -->
            <Row>
              <Row style="padding-bottom:10px;padding-right:10px; box-sizing: border-box;">
                <Button
                  type="dashed"
                  icon="md-add"
                  size="small"
                  style="float:right"
                  @click="addpeoplefun"
                  :disabled="addFlag"
                >新增</Button>
              </Row>
              <Row class="flexStart">
                <!-- <Row class="processAddPeople1">被调查人</Row> -->
                <Row class>
                  <Row
                    v-for="(item,index) in addPeopleArr"
                    class="flexStart"
                    :key="index"
                    style="border:1px dashed #E8EAEC"
                  >
                    <Row style="width:70%" class="flexStartWrap">
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户号</p>
                        <Input
                          v-model="item.clientId"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">源客户号</p>
                        <Input
                          v-model="item.clientOldId"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户名称</p>
                        <Input
                          v-model="item.clientName"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件类型</p>
                        <Input
                          v-model="item.certificateType"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件号码</p>
                        <Input
                          v-model="item.certificateId"
                          placeholder
                          style="width:50%"
                          size="small"
                          :disabled="addFlag"
                        />
                      </Row>
                    </Row>
                    <Row style="width:30%;text-align: center;">
                      <Button
                        type="dashed"
                        icon="ios-close"
                        size="small"
                        style="float:right;margin-right:10px;margin-top:23px"
                        @click="delPeoplefun(index)"
                        :disabled="addFlag"
                      >删除</Button>
                    </Row>
                  </Row>
                </Row>
              </Row>
            </Row>
            <!-- ============== -->
          </FormItem>
          <FormItem label="冻结客户个数" prop="val8">
            <Input v-model="formValidateModel1.val8" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="案件类型" prop="val9">
            <Input v-model="formValidateModel1.val9" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="涉及金额" prop="val10">
            <Input v-model="formValidateModel1.val10" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="交易笔数" prop="val15">
            <Input v-model="formValidateModel1.val15" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="业务名称" prop="val12">
            <Input v-model="formValidateModel1.val12" placeholder="请输入" :disabled="this.addFlag"></Input>
          </FormItem>
          <FormItem label="是否公开">
            <i-switch v-model="formValidateModel1.val13" size="large" :disabled="this.addFlag">
              <span slot="true">On</span>
              <span slot="false">Off</span>
            </i-switch>
          </FormItem>
          <FormItem label="编辑内容" prop="val14">
            <Input
              v-model="formValidateModel1.val14"
              type="textarea"
              :autosize="{minRows: 2,maxRows: 5}"
              placeholder="请输入"
              :disabled="this.addFlag"
            ></Input>
          </FormItem>
          <FormItem label="上传附件">
            <showUsualFiles
              :file-obj="dataObj"
              :key="fileId"
              @requestUploadList="receiveUploadList"
            ></showUsualFiles>
          </FormItem>
          <Row v-if="addFlagShow">
            <Row style="text-again:left;border-bottom:1px dashed #E8EAEC;margin-bottom:20px">追加信息</Row>
            <FormItem label="上传附件">
              <showUsualFiles
                :file-obj="dataObj2"
                :key="fileId2"
                @requestUploadList="receiveUploadList2"
              ></showUsualFiles>
            </FormItem>
            <FormItem label="追加内容">
              <Input
                v-model="formValidateModel1.val11"
                type="textarea"
                :autosize="{minRows: 2,maxRows: 5}"
                placeholder="请输入"
              ></Input>
            </FormItem>
          </Row>
          <Modal :mask-closable="false" v-model="taskPeipleModel" width="360" class="treeBoxModel">
            <p slot="header" style="color:#f60;text-align:center">
              <span>审核人信息</span>
            </p>
            <div style="text-align:left;overflow-y:auto;height:300px">
              <FormItem prop="nextUser" :label-width="10">
                <Tree
                  ref="treeRef"
                  :data="data2"
                  show-checkbox
                  multiple
                  @on-check-change="updateNextUser"
                ></Tree>
              </FormItem>
            </div>
            <div slot="footer">
              <Button
                type="primary"
                size="large"
                long
                @click="handleSubmitLast('formValidateModel1')"
                :disabled="buttonDisabled"
              >立即申请</Button>
            </div>
          </Modal>
        </Form>
      </div>
      <div slot="footer" class="flexCenter">
        <div class="button-content">
          <Button type="primary" @click="save1('formValidateModel1')">{{buttonVal}}</Button>
          <Button @click="modal1=false">取消</Button>
        </div>
      </div>
    </Modal>
    <!-- ============== -->
    <Modal :mask-closable="false" v-model="watchFileModel" width="90%" title="查看">
      <showUsualFiles :file-obj="dataObj3" :key="fileId3" @requestUploadList="receiveUploadList3"></showUsualFiles>
    </Modal>
    <!-- ===========弹框 -->
    <!-- ============== -->
    <Modal
      :mask-closable="false"
      v-model="watchTableCellDitalModel"
      width="90%"
      title="查看"
      footer-hide
    >
      <div class="content">
        <Form :label-width="120">
          <FormItem label="流程标题：">
            <label>{{watchTableCell.bpmTitle}}</label>
          </FormItem>
          <FormItem label="协助事项：">
            <!-- <CheckboxGroup v-model="watchTableCell.val1">
              <Checkbox v-for="item in assistWorks" :label="item.code" :key="item.code" disabled>{{item.name}}</Checkbox>
            </CheckboxGroup>-->
            <label>{{watchTableCell.val1}}</label>
          </FormItem>
          <FormItem label="经办人：">
            <label>{{watchTableCell.val2}}</label>
          </FormItem>
          <FormItem label="执行机构：">
            <label>{{watchTableCell.val3}}</label>
          </FormItem>
          <FormItem label="执行省份：">
            <label>{{watchTableCell.val4}}</label>
          </FormItem>
          <FormItem label="执行人：">
            <label>{{watchTableCell.val5}}</label>
          </FormItem>
          <FormItem label="执行人证件号码：">
            <label>{{watchTableCell.val6}}</label>
          </FormItem>
          <FormItem label="协查日期：">
            <label>{{watchTableCell.val7}}</label>
          </FormItem>
          <FormItem label="被调查人">
            <!-- ============== -->
            <Row>
              <Row class="flexStart">
                <!-- <Row class="processAddPeople1">被调查人</Row> -->
                <Row class>
                  <Row
                    v-for="(item,index) in addPeopleArr"
                    class="flexStart"
                    :key="index"
                    style="border:1px dashed #E8EAEC"
                  >
                    <Row style="width:70%" class="flexStartWrap">
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户号</p>
                        <Input
                          v-model="item.clientId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">源客户号</p>
                        <Input
                          v-model="item.clientOldId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">客户名称</p>
                        <Input
                          v-model="item.clientName"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件类型</p>
                        <Input
                          v-model="item.certificateType"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                      <Row class="flexCenter processAddPeople-cell-cell">
                        <p class="processAddPeople3">证件名称</p>
                        <Input
                          v-model="item.certificateId"
                          placeholder
                          style="width:50%"
                          size="small"
                          disabled
                        />
                      </Row>
                    </Row>
                  </Row>
                </Row>
              </Row>
            </Row>
            <!-- ============== -->
          </FormItem>
          <FormItem label="冻结客户个数：">
            <label>{{watchTableCell.val8}}</label>
          </FormItem>
          <FormItem label="案件类型：">
            <label>{{watchTableCell.val9}}</label>
          </FormItem>
          <FormItem label="涉及金额：">
            <label>{{watchTableCell.val10}}</label>
          </FormItem>
          <FormItem label="交易笔数：">
            <label>{{watchTableCell.val15}}</label>
          </FormItem>
          <FormItem label="业务名称：">
            <label>{{watchTableCell.val12}}</label>
          </FormItem>
          <FormItem label="是否公开：">
            <label>{{watchTableCell.val13=='true'?"是":'否'}}</label>
