  methods: {
    download(data, name) {
      if (!data) {
        return;
      }
      let url = window.URL.createObjectURL(new Blob([data]));
      let link = document.createElement("a");
      link.style.display = "none";
      link.href = url;
      link.setAttribute("download", name);
      document.body.appendChild(link);
      link.click();
    },
    exportFn() {
      let that = this;
      let data = {
        executeOrg: this.formValidate.val2,
        executeProvince: this.formValidate.val3 ? this.formValidate.val3 : "",
        executor: this.formValidate.val4,
        maxAgentTime: this.formValidate.val5[1],
        minAgentTime: this.formValidate.val5[0],
        respondent: this.formValidate.val6,
        businessName: this.formValidate.val7,
        flag: this.formValidate.val8,
        manager: JSON.stringify(this.formValidate.val9),
        ifExacte: this.ifExacte,
        pageNum: this.pageNum,
        pageSize: this.pageSize
      };
      data = JSON.stringify(data);
      this.axios
        .post(
          "/process/infCaseInvestigation/getInfCaseInvestigationExcelList",
          data,
          {
            headers: { "Content-Type": "application/json" },
            responseType: "blob"
          }
        )
        .then(response => {
          this.download(response, "导出文档.xls");
        })
        .catch(error => {});
